import axios from 'axios'

const ERR_OK = 0

/**
 * get helperget https://juejin.im/post/5b55c118f265da0f6f1aa354
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get1 (url, params) {
  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    }).then(res => {
      resolve(res.data)
    }).catch(err => {
      reject(err.data)
    })
  })
}

export function get (url) {
  return function (params) {
    return axios.get(url, {
      params
    }).then((res) => {
      const {
        errno,
        data
      } = res.data
      if (errno === ERR_OK) {
        return data
      }
    }).catch((err) => {
      return err
    })
  }
}
